import React, { Component } from 'react';
import PropTypes from 'prop-types';
import withRoot from '../../withRoot';

// Material-ui
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import CircularProgress from '@material-ui/core/CircularProgress'

// Redux state
import { connect } from "react-redux";

// Helpers JSX
import Loading from '../helpers/Loading'

const styles = theme => ({
    facebook2: {
        color: '#6798e5',
        animationDuration: '550ms',
        position: 'absolute',
        left: 0,
    },
});
  
// Redux mappings
const mapState = state => ({
});
  
const mapDispatch = dispatch => ({
});

class NewIdentityDialog extends Component {

    constructor(props) {
        super(props);

        this.state = {
            username: '',
            password: '',
            confirm_password: '',
            creating_account: false
        };
    }

    handleChange = name => event => {
        this.setState({
            [name]: event.target.value,
        });
    };

    generateNewAccount = async () => {
        let account_password = this.state.password
        this.setState({
            creating_account: true
        })
        this.setState({
            creating_account: false
        })

        // Close
        this.props.onCloseHandler()
        return
    }

    render() {

        const { 
            open, 
            onCloseHandler,
            classes
        } = this.props;
        const {
            username,
            password,
            confirm_password,
            creating_account
        } = this.state

        const validPassword = Boolean(password === confirm_password)
        const validUsername = Boolean(username)

        return (
            <div>
                <Dialog
                    open={open}
                    onClose={onCloseHandler}
                    aria-labelledby="form-dialog-title"
                >
                    <DialogTitle id="form-dialog-title">Create new account</DialogTitle>
                    <DialogContent>
                    <DialogContentText>
                        Select a username
                    </DialogContentText>
                    <TextField
                        autoFocus
                        margin="dense"
                        id="username"
                        label="Username"
                        type="name"
                        onChange={this.handleChange('username')}
                        fullWidth
                    />
                    { validUsername && (<TextField
                        margin="dense"
                        id="password"
                        label="Password"
                        type="password"
                        onChange={this.handleChange('password')}
                        fullWidth
                    />) }
                    {password && 
                    (<TextField
                        margin="dense"
                        id="confirm-password"
                        label="Confirm Password"
                        type="password"
                        onChange={this.handleChange('confirm_password')}
                        fullWidth
                    />)}
                    </DialogContent>
                    <DialogActions>
                    {creating_account ? (<Loading />) : 
                    (<Button onClick={onCloseHandler} color="primary">
                        Nevermind
                    </Button>)}
                    {creating_account ? 
                    (<Button
                        disabled
                        color="primary"
                    >
                        <CircularProgress
                            variant="indeterminate"
                            disableShrink
                            className={classes.facebook2}
                            size={24}
                            thickness={4}
                        />
                    </Button>)
                    : 
                    (<Button 
                        onClick={this.generateNewAccount} 
                        color="primary"
                        disabled={!validPassword}
                    >
                        Create
                    </Button>)}
                    </DialogActions>
                </Dialog>
            </div>
        );
    }
}

NewIdentityDialog.propTypes = {
    classes: PropTypes.object.isRequired,
    open: PropTypes.bool.isRequired,
    onCloseHandler: PropTypes.func.isRequired,
};

export default connect(mapState, mapDispatch)(withRoot(withStyles(styles)(NewIdentityDialog)));
